import React, { FC } from 'react';
import { useSelector } from 'react-redux';
import {
  boolColumn,
  ColumnsProps,
  DefaultSortProps,
  FilterType,
  getExtraData,
  OrderType,
  SearchDataGrid,
  SearchExpression,
  SearchViewProps,
  WindowComponent,
} from 'summer';
import { hasEmpresaMFP } from '../../../../dependencies/empresaMFPDependency';
import { CLASS_NAME, QuadroHorario } from '../../../../models/dtos/mfp/quadroHorario';
import { Reducers } from '../../../../store/ducks';
import { GlobalState } from '../../../../store/ducks/global.duck';

const defaultSorted: DefaultSortProps[] = [
  {
    dataField: 'codigo',
    order: OrderType.DESC,
  },
];

const QuadroHorarioSearchView: FC<SearchViewProps<QuadroHorario>> = props => {
  const globalReducer = useSelector<Reducers, GlobalState>(state => state.globalReducer);

  const columns: ColumnsProps[] = [
    {
      dataField: 'codigo',
      editable: false,
      filter: {
        type: FilterType.NUMBER,
      },
      sort: true,
      text: 'Código',
    },
    {
      dataField: 'dataInatividade',
      editable: false,
      filter: {
        type: FilterType.DATE,
      },
      sort: true,
      text: 'Data Inatividade',
    },
    boolColumn({
      dataField: 'escala',
      text: 'Escala',
    }),
    {
      dataField: 'descricao',
      editable: false,
      filter: {
        type: FilterType.TEXT,
      },
      sort: true,
      text: 'Descrição',
    },
  ];

  const getSearchExpression = (): SearchExpression[] => {
    return [
      {
        fieldName: 'empresaMFP.id',
        value: globalReducer.globalParameter.empresaMFP.id,
      },
    ];
  };

  return (
    <WindowComponent
      title="Manutenção de Quadro de Horário"
      useGlobalParameter={true}
      globalParameter={globalReducer.globalParameter}
      dependencies={[() => hasEmpresaMFP(globalReducer.globalParameter)]}
    >
      <SearchDataGrid
        {...props}
        {...props.searchDataGridProps}
        columns={columns}
        defaultSorted={defaultSorted}
        dtoClassName={CLASS_NAME}
        extraData={getExtraData(props, 'QuadroHorarioEditView', CLASS_NAME)}
        searchExpressions={getSearchExpression}
      />
    </WindowComponent>
  );
};

export default QuadroHorarioSearchView;
